/* 
	語意化 
*/

i.xiaoan.rainbow {
    background: linear-gradient(to bottom, #f00, #f0f 15%, #00f 33%, #0ff 49%, #0f0 67%, #ff0 84%, #f00);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.ts.xiaoan.primary {
    filter: invert(.5) sepia(1) hue-rotate(170deg);
}

.ts.xiaoan.info {
    filter: invert(.5) sepia(1) hue-rotate(220deg);
}

.ts.xiaoan.warning {
    filter: invert(.7) sepia(1) saturate(2.8) brightness(1) hue-rotate(10deg);
}

.ts.xiaoan.positive {
    filter: invert(.7) sepia(1) brightness(.7) hue-rotate(10deg);
}

.ts.xiaoan.negative {
    filter: invert(.7) sepia(1) saturate(5) brightness(.5) hue-rotate(300deg);
}

.ts.xiaoan.inverted {
    filter: invert(0.92);
}

.ts.xiaoan.exceedingly.light.blue.xiaoan {
    filter: invert(0.9) contrast(65%) sepia(100%) hue-rotate(129.5deg) grayscale(0%) saturate(100%);
}

.ts.xiaoan.gloomy {
    filter: contrast(25%) sepia(53%) hue-rotate(165.5deg) brightness(80%) saturate(100%);
}

.ts.xiaoan.NTR {
    filter: invert(0.87) contrast(32%) sepia(1) hue-rotate(75.5deg) grayscale(0%) blur(1.1px) brightness(87%) saturate(100%);
}

.ts.xiaoan.love {
    filter: invert(0.95) contrast(32%) sepia(100%) hue-rotate(266.5deg) grayscale(0%) blur(0px) brightness(100%) saturate(100%);
}

.ts.xiaoan.EarlySpring {
    filter: invert(1) contrast(200%) hue-rotate(0deg) grayscale(0%) blur(0px) brightness(100%) saturate(0%);
}


/*
	動畫
	------------------
				 速度
	fast 				 快
	slow 				 慢
	early spring 夭壽慢
	-----------------
	簡寫
	animation: name duration timing-function delay iteration-count direction;
	-----------------
*/


/* 震動 */

.shake {
    animation: shake .01s linear infinite;
}

.shake.fast {
    animation-duration: .005s;
}

.shake.slow {
    animation-duration: .02s;
}

.shake.early.spring {
    animation-duration: 40s;
}

@keyframes shake {
    0%,
    100% {
        transform: translate(0, 0) rotate(0);
        opacity: 1;
    }
    25% {
        transform: translate(-2.5px, -2.5px) rotate(-1.52deg);
        opacity: 0.95;
    }
    40% {
        transform: translate(-2.5px, 2.5px) rotate(2.5deg);
        opacity: 1;
    }
    50% {
        transform: translate(2.5px, 2.5px) rotate(-1.75deg);
        opacity: 0.95;
    }
    60% {
        transform: translate(0px, 0px) rotate(5deg);
        opacity: 1;
    }
    75% {
        transform: translate(2.5px, -2.5px) rotate(2.5deg);
        opacity: 0.7;
    }
}


/* 旋轉 */

.rotate {
    animation: rotate .75s linear infinite;
}

.rotate.fast {
    animation-duration: .25s;
}

.rotate.slow {
    animation-duration: 1.5s;
}

.rotate.early.spring {
    animation-duration: 100s;
}

@keyframes rotate {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}


/* 嗑藥 */

i.xiaoan.drug {
    background: linear-gradient(to bottom, #f00, #f0f 15%, #00f 33%, #0ff 49%, #0f0 67%, #ff0 84%, #f00);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: drug 1s linear infinite, shake 10ms linear infinite, pulse-animation 10ms linear infinite, 2s linear infinite icon-loading;
    background-size: 700% 700% !important
}

@keyframes drug {
    0% {
        background-position: 50% 0%
    }
    100% {
        background-position: 50% 100%
    }
}


/* ㄉㄨㄞㄉㄨㄞ安 */

.poyon {
    animation: poyon 0.5s infinite;
}

.poyon.crazy {
    animation: crazypoyon 0.5s infinite;
}

.poyon.fast {
    animation-duration: .25s;
}

.poyon.slow {
    animation-duration: 1.5s;
}

.poyon.early.spring {
    animation-duration: 100s;
}

@keyframes poyon {
    0%,
    100% {
        transform: scale(1, 1);
    }
    25% {
        transform: scale(0.9, 1.1);
    }
    50% {
        transform: scale(1.1, 0.9);
    }
    75% {
        transform: scale(0.95, 1.05);
    }
}

@keyframes crazypoyon {
    0%,
    100% {
        transform: scale(1, 1) translateX(10px) rotate(5deg);
    }
    25% {
        transform: scale(0.9, 1.7) translateY(-10px) translateZ(-10px);
    }
    50% {
        transform: scale(1.7, 0.8) translateX(-10px) rotate(-5deg);
    }
    75% {
        transform: scale(0.95, 1.05) translateY(10px);
    }
}


/* 蹲跳安 */

.squatting {
    animation: squatting 2s infinite;
}

.squatting.fast {
    animation-duration: 1s;
}

.squatting.slow {
    animation-duration: 3s;
}

.squatting.early.spring {
    animation-duration: 200s;
}

@keyframes squatting {
    0%,
    100% {
        transform: scale(1, 1);
        background-color: transparent;
    }
    5% {
        transform: scale(1, .1) translateY(300px);
    }
    25% {
        transform: scale(1.6, 2) translateY(-300px);
    }
    40% {
        transform: scale(1, .5) translateY(100px);
    }
    50% {
        transform: scale(1, 1);
    }
}


/* 邊緣安 */

.edge {
    animation: edge 5s infinite;
}

.edge.fast {
    animation-duration: 2.5s;
}

.edge.slow {
    animation-duration: 7.5s;
}

.edge.early.spring {
    animation-duration: 500s;
}

@keyframes edge {
    0%,
    100% {
        opacity: 0.01;
    }
    50% {
        opacity: 0.05;
    }
}


/* 
	卷軸
*/

.ts.scrollbar::-webkit-scrollbar {
    height: 20px;
    width: 20px;
    background: #f5f5f5;
    box-shadow: inset 0 0 7px 0px rgba(0, 0, 0, 0.1);
}

.ts.scrollbar::-webkit-scrollbar-thumb {
    background: url(scrollbar/xiaoan.png) #b3b2b2 center;
    background-size: 20px;
}

.ts.scrollbar::-webkit-scrollbar-button:start {
    background: url(scrollbar/start.png) #b3b2b2 center no-repeat;
    background-size: 15px;
}

.ts.scrollbar::-webkit-scrollbar-button:end {
    background: url(scrollbar/end.png) #b3b2b2 center no-repeat;
    background-size: 15px;
}

.ts.scrollbar::-webkit-scrollbar:hover {
    box-shadow: inset 0 0 7px 0px rgba(0, 0, 0, 0.2);
}

.ts.scrollbar::-webkit-scrollbar:active {
    box-shadow: inset 0 0 7px 0px rgba(0, 0, 0, 0.3);
}

.ts.scrollbar::-webkit-scrollbar-thumb:hover,
.ts.scrollbar::-webkit-scrollbar-button:horizontal:hover,
.ts.scrollbar::-webkit-scrollbar-button:hover {
    background-color: #8e8e8e;
}

.ts.scrollbar::-webkit-scrollbar-thumb:active,
.ts.scrollbar::-webkit-scrollbar-button:active {
    background-color: #6f6f6f;
}


/* 水平卷軸修正 */

.ts.scrollbar::-webkit-scrollbar-thumb:horizontal {
    background: url(scrollbar/xiaoan-horizontal.png) #b3b2b2 center;
    background-size: 25px;
}

.ts.scrollbar::-webkit-scrollbar-button:horizontal:start {
